.g--card-44{
    @include make-card-44();
    @include make-card-44-modifier(
        $background-color:lighten(map-get($color-options,a), 82%),
        $border-color: map-get($color-options, a),
        $border-style: solid,
        $border-width: 1px,
        $border-radius: 0,
        $breakpoint: $tablets,
        $hover-background-color: rgba(map-get($color-options, h), .5),
        $hover-img-zoom: 1.4,
        $hover-shadow: 0 0 $measure*5 map-get($color-options, h),
        $hover-title-color: map-get($color-options, g),
        $hover-transform: translateY(-$measure),
        // $img-aspect-ratio-responsive: 1,
        $img-height-responsive: 300px,
        $img-position: left bottom,
        $title-color: map-get($color-options, a),
        $title-font: c,
        $title-font-weight: 400,
       $text-align: left,
        $content-color: map-get($color-options, a),
        $content-font: f,
        $content-font-weight: 400,
        $content-max-lines: 3,
        $change-order-reponsive: true
    );
    &--second{
        @include make-card-44-modifier(
            $background-color: transparent,
            $border-style: none,
            $hover-background-color: transparent,
            $hover-img-zoom: 1.1,
            $hover-shadow: none,
            $hover-transform: none,
            $img-position: center,
            $title-color: map-get($color-options, b),
            $title-font: d,
            $content-color: map-get($color-options, b),
        );
    }
}