.g--card-04{
    @include make-card-04();
    @include make-card-04-modifier(
        $background-color:lighten(map-get($color-options,a), 82%),
        $border-color: map-get($color-options, a),
        $border-style: solid,
        $border-width: 1px,
        $link-class: g--link-01,
        $title-color: map-get($color-options, a),
        $title-font: c,
    );
    &--second{
        @include make-card-04-modifier(
            $background-color: rgba(map-get($color-options, g), .7),
            $subtitle-color: map-get($color-options, a),
            $title-color: map-get($color-options, a),
            $title-font: c,
            // $border-radius: false,
            $hover-background-color: map-get($color-options, g),
            $hover-img-zoom: 1.2,
            $hover-shadow: 0 0 $measure*2 map-get($color-options, h),
            $hover-title-color: map-get($color-options, h),
            $hover-transform: translateY(-$measure),
            $img-aspect-ratio: calc(1 / 1),
            $img-height: 100px,
            $img-fit: cover,
            $img-position: center,
            $img-width: auto,
            $link-class: g--btn-01--second,
            // $link-font: false,
            $subtitle-font: f,
            $subtitle-font-weight: 400,
            $subtitle-max-lines: 3,
           $text-align: left,
            $title-font-weight: 400,
            $title-max-lines: 3,
        );
    }
    &--third{
        @include make-card-04-modifier(
            $background-color: rgba(map-get($color-options, e), .2),
            $border-color: map-get($color-options, e),
            $border-style: dotted,
            $border-width: 1px,
            $border-radius: $measure*3 $measure*6,
            $link-class: g--link-01,
            $title-color: map-get($color-options, a),
            $title-font: c,
            $title-max-lines: 3,
        );
    }
    &--fourth{
        @include make-card-04-modifier(
            $background-color: rgba(map-get($color-options, g), .7),
            $border-color: map-get($color-options, a),
            $border-style: solid,
            $border-width: 0 0 8px,
            $subtitle-color: map-get($color-options, a),
            $title-color: map-get($color-options, a),
            $title-font: c,
            // $border-radius: false,
            $hover-background-color: map-get($color-options, g),
            $hover-img-zoom: 1.2,
            $hover-shadow: 0 0 $measure*2 map-get($color-options, h),
            $hover-title-color: map-get($color-options, h),
            $hover-transform: translateY(-$measure),
            $img-aspect-ratio: calc(1 / 1),
            $img-height: 100px,
            $img-fit: cover,
            $img-position: center,
            $img-width: auto,
            $link-class: g--btn-01--second,
            // $link-font: false,
            $subtitle-font: f,
            $subtitle-font-weight: 400,
            $subtitle-max-lines: 3,
           $text-align: left,
            $title-font-weight: 400,
            $title-max-lines: 3,
        );
    }
}