.g--card-25 {
    @include make-card-25();
    @include make-card-25-modifier(
        $background-color: lighten(map-get($color-options,a), 82%),
        $img-aspect-ratio: calc(16 / 9),
        $img-height: 340px,
    );
    &--second{
        @include make-card-25-modifier(
            $background-color: rgba(map-get($color-options, a), 5),
            $border-radius: $measure*2 $measure*4,
            $full-image-height: false,
            $hover-background-color: map-get($color-options, h),
            $hover-img-zoom: 1.2,
            $hover-shadow: 0 0 $measure*4 map-get($color-options, h),
            $hover-title-color: map-get($color-options, a),
            // $img-aspect-ratio: false,
            $img-height: false,
            $img-position: false,
           $text-align: left,
            $title-color: map-get($color-options, b),
            $title-font: false,
            $title-font-weight: false,
            $title-max-lines: 6,
            $hover-transform: translateY(-5%)
        );
    }
}