@mixin make-card-03() {
    padding: $measure*7 $measure*5;
    width: 100%;
    display: block;
    position: relative;
    overflow: hidden;
    text-decoration: none;
    &::after{
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;
    }
    &__bg-items{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;
        background-repeat: no-repeat;
    }
    &__ft-items{
        position: relative;
        z-index: 1;
        &__media-wrapper{
            overflow: hidden;
            margin-bottom: $measure * 2;
            &__media {
                display: block;
                @media all and ($viewport-type: $tablets) {
                    margin-bottom: $measure;
                }
            }
        }
    
        &__title {
            margin-bottom: $measure * 2;
            @media all and ($viewport-type: $tablets) {
                margin-bottom: $measure;
            }
        }
    }
}

@mixin make-card-03-modifier(
    $background-position: false,
    $background-size: false,
    $border-radius: false,
    $hover-background-img-zoom: false,
    $hover-img-zoom: false,
    $hover-shadow: false,
    $hover-title-color: false,
    $hover-transform: false,
    $img-aspect-ratio: false,
    $img-height: false,
    $img-position: false,
    $img-fit: false,
    $img-width: false,
    $mask-color: false,
    $subtitle-color: false,
    $subtitle-font: false,
    $subtitle-font-weight: false,
    $subtitle-max-lines: false,
    $text-align: false,
    $title-color: false,
    $title-font: false,
    $title-font-weight: false,
    $title-max-lines: false,

) {

    @if $hover-shadow and $hover-transform {
        transition: box-shadow $time-b $ease-standard-a, transform $time-b $ease-standard-a;
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                box-shadow: $hover-shadow;
                transform: $hover-transform;
            }
        }
    } @else if $hover-shadow {
        transition: box-shadow $time-b $ease-standard-a;
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                box-shadow: $hover-shadow;
            }
        }
    } @else if $hover-transform {
        transition: transform $time-b $ease-standard-a;
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                transform: $hover-transform;
            }
        }
    }
    @if $hover-title-color {
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                .g--card-03{
                    &__ft-items{
                        &__title{
                            color: $hover-title-color;
                        }
                    }
                }
            }
        }
    }
    @if $hover-img-zoom {
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                .g--card-03{
                    &__ft-items{
                        &__media-wrapper{
                            &__media{
                                transform: scale($hover-img-zoom);
                            }
                        }
                    }
                }
            }
        }
    }
    @if $hover-background-img-zoom {
        .g--card-03{
            &__bg-items{
                transition: transform $time-b $ease-standard-a;
            }
        }
        @media all and (hover : hover) {
            &:hover,
            &:focus,
            &:active {
                .g--card-03{
                    &__bg-items{
                        transform: scale($hover-background-img-zoom);
                    }
                }
            }
        }
    }
    @if $border-radius {
        border-radius: $border-radius;
    }
    @if $background-position {
        .g--card-03{
            &__bg-items{
                background-position: $background-position;
            }
        }
    }
    @if $background-size {
        .g--card-03{
            &__bg-items{
                background-size: $background-size;
            }
        }
    }

    @if $mask-color {
        &::after{
            background: $mask-color;
        }
    }

    
    @if $text-align == "center" {
        text-align: center;
        .g--card-03{
            &__ft-items{
                &__media-wrapper{
                    margin-left: auto;
                    margin-right: auto;
                    &__media{
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
            }
        }
        
    }
    @if $text-align == "left" {
        text-align: left;
        .g--card-03{
            &__ft-items{
                &__media-wrapper{
                    margin-left: 0;
                    margin-right: auto;
                    &__media{
                        margin-left: 0;
                        margin-right: auto;
                    }
                }
            }
        }
    }
    @if $text-align == "right" {
        text-align: right;
        .g--card-03{
            &__ft-items{
                &__media-wrapper{
                    margin-left: auto;
                    margin-right: 0;
                    &__media{
                        margin-left: auto;
                        margin-right: 0;
                    }
                }
            }
        }
    }
    .g--card-03{
        &__ft-items{
            &__media-wrapper{
                @if $img-aspect-ratio {
                    aspect-ratio: $img-aspect-ratio;
                }
                @if $img-width {
                    width: $img-width;
                }
                @if $img-height {
                    height: $img-height;
                }
                &__media {
                    @if $img-aspect-ratio {
                        aspect-ratio: $img-aspect-ratio;
                    }
                    @if $img-width {
                        width: 100%;
                    }
                    @if $img-height {
                        height: 100%;
                    }
                    @if $img-fit{
                        object-fit: $img-fit;
                    }
                    @if $img-position{
                        object-position: $img-position;
                    }
                    @if $hover-img-zoom {
                        transition: transform $time-b $ease-standard-a;
                    }
                }
            }
        
            &__title {
                @if $title-color {
                    color: $title-color;
                }
                @if $title-font {
                    @extend .f--font-#{$title-font} !optional;
                }
                @if $title-font-weight {
                    font-weight: $title-font-weight;
                }
                @if $title-max-lines {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: $title-max-lines;
                    -webkit-box-orient: vertical;
                }
                @if $hover-title-color {
                    transition: color $time-b $ease-standard-a;
                }
            }
        
            &__subtitle {
                @if $subtitle-color {
                    color: $subtitle-color;
                }
                @if $subtitle-font {
                    @extend .f--font-#{$subtitle-font} !optional;
                }
                @if $subtitle-font-weight {
                    font-weight: $subtitle-font-weight;
                }
                @if $subtitle-max-lines {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: $subtitle-max-lines;
                    -webkit-box-orient: vertical;
                }
            }
        }
    }
}