.g--card-10{
    @include make-card-10();
    @include make-card-10-modifier(
        $background-color: lighten(map-get($color-options,a), 82%),
        $link-class: g--link-01,
        $title-color: map-get($color-options, a),
        $title-font: c,
    );
    &--second{
        @include make-card-10-modifier(
            $background-color: linear-gradient(217deg, map-get($color-options, e), map-get($color-options, g) 70.71%),
            $border-color: map-get($color-options, a),
            // $border-style: false,
            $border-width: 0 0 8px,
            $title-color: map-get($color-options, a),
            $title-font: c,
            // $border-radius: false,
            $hover-background-color: map-get($color-options, g),
            $hover-img-zoom: 1.2,
            $hover-shadow: 0 0 $measure*2 map-get($color-options, h),
            $hover-title-color: map-get($color-options, h),
            $hover-transform: translateY(-$measure),
            $img-aspect-ratio: calc(1 / 1),
            $img-height: 100px,
            $img-fit: cover,
            $img-position: center,
            $img-width: auto,
            // $link-class: g--btn-01--second,
            // $link-font: false,
           $text-align: left,
            $title-font-weight: 400,
            $title-max-lines: 3,
        );
    }
    &--third{
        @include make-card-10-modifier(
            $border-color: map-get($color-options, a),
            $border-style: solid,
            $border-width: 1px,
            $link-class: g--link-01,
            $title-color: map-get($color-options, a),
            $title-font: c,
        );
    }
    &--fourth{
        @include make-card-10-modifier(
            $background-color: rgba(map-get($color-options, g), .7),
            $border-color: map-get($color-options, a),
            $border-style: solid,
            $border-width: 8px,
            $title-color: map-get($color-options, a),
            // $border-radius: false,
            $hover-background-color: map-get($color-options, g),
            $hover-img-zoom: 1.2,
            $hover-shadow: 0 0 $measure*2 map-get($color-options, a),
            $hover-title-color: map-get($color-options, a),
            $hover-transform: translateY(-$measure),
            $img-aspect-ratio: calc(1 / 1),
            $img-height: 100px,
            $img-fit: cover,
            $img-position: center,
            $img-width: auto,
            $link-class: g--btn-01--third,
            // $link-font: false,
           $text-align: left,
            $title-font-weight: 400,
            $title-max-lines: 3,
        );
    }
}